.app {
    min-height: 100vh;
    background: #0b1020;
    color: #f7f7f7;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
}

.app-header {
    padding: 1.5rem 2rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
    background: radial-gradient(circle at top left, #1d3557, #020413);
}

.app-header h1 {
    margin: 0;
    font-size: 1.8rem;
}

.app-header p {
    margin: 0.3rem 0 0;
    font-size: 0.9rem;
    opacity: 0.8;
}

.layout {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: 1.5rem;
    padding: 1.5rem 2rem 2rem;
}

@media (max-width: 900px) {
    .layout {
        grid-template-columns: 1fr;
    }
}

.main {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.controls {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 0.5rem;
}

.controls select {
    padding: 0.25rem 0.5rem;
    border-radius: 6px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    background: #111827;
    color: #f9fafb;
}

.controls button,
.file-input-label {
    padding: 0.3rem 0.7rem;
    border-radius: 6px;
    border: 1px solid rgba(148, 163, 184, 0.7);
    background: #111827;
    color: #e5e7eb;
    font-size: 0.8rem;
    cursor: pointer;
}

.controls button:hover,
.file-input-label:hover {
    border-color: rgba(209, 213, 219, 0.9);
}

.file-input-label {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
}

.status-badge {
    padding: 0.2rem 0.5rem;
    border-radius: 999px;
    font-size: 0.8rem;
    background: #1f2937;
}

.status-badge.error {
    background: #7f1d1d;
}

.empty-state {
    padding: 1rem;
    border-radius: 8px;
    background: rgba(15, 23, 42, 0.8);
    border: 1px dashed rgba(148, 163, 184, 0.5);
    font-size: 0.9rem;
}

.games-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.game-card {
    background: #020617;
    border-radius: 12px;
    padding: 0.75rem 1rem 0.75rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
    box-shadow: 0 6px 14px rgba(15, 23, 42, 0.7);
}

.game-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
    font-size: 0.8rem;
    color: #e5e7eb;
}

.game-status {
    padding: 0.15rem 0.5rem;
    border-radius: 999px;
    font-size: 0.7rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
}

.teams {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    margin-bottom: 0.6rem;
}

.team-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.team-info {
    display: flex;
    flex-direction: column;
}

.team-label {
    font-size: 0.7rem;
    text-transform: uppercase;
    opacity: 0.7;
}

.team-name {
    font-size: 0.9rem;
}

.team-score {
    font-weight: 600;
    font-size: 1.1rem;
}

.predictions {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
    gap: 0.4rem 0.8rem;
    margin-top: 0.4rem;
    padding-top: 0.4rem;
    border-top: 1px solid rgba(55, 65, 81, 0.7);
}

.prediction-row {
    display: flex;
    flex-direction: column;
    gap: 0.15rem;
}

.prediction-user {
    font-size: 0.75rem;
    opacity: 0.8;
}

.prediction-row select {
    padding: 0.25rem 0.4rem;
    border-radius: 6px;
    border: 1px solid rgba(148, 163, 184, 0.7);
    background: #020617;
    color: #e5e7eb;
    font-size: 0.8rem;
}

/* Sidebar / standings */

.sidebar {
    min-width: 0;
}

.controls button,
.file-input-label {
    padding: 0.3rem 0.7rem;
    border-radius: 6px;
    border: 1px solid rgba(148, 163, 184, 0.7);
    background: #111827;
    color: #e5e7eb;
    font-size: 0.8rem;
    cursor: pointer;
}

.controls button:hover,
.file-input-label:hover {
    border-color: rgba(209, 213, 219, 0.9);
}

.file-input-label {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
}

.standings {
    background: #020617;
    border-radius: 12px;
    padding: 0.9rem 1rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
    box-shadow: 0 8px 20px rgba(15, 23, 42, 0.8);
}

.standings h2 {
    margin-top: 0;
    margin-bottom: 0.5rem;
    font-size: 1.1rem;
}

.standings-empty {
    font-size: 0.9rem;
    opacity: 0.9;
}

.standings-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.85rem;
    margin-top: 0.4rem;
}

.standings-table th,
.standings-table td {
    padding: 0.35rem 0.35rem;
    text-align: left;
}

.standings-table thead {
    border-bottom: 1px solid rgba(148, 163, 184, 0.5);
}

.standings-table tbody tr:nth-child(even) {
    background: rgba(15, 23, 42, 0.9);
}

.standings-table tbody tr:nth-child(odd) {
    background: rgba(15, 23, 42, 0.6);
}

.standings-note {
    margin-top: 0.5rem;
    font-size: 0.7rem;
    opacity: 0.7;
}

/* Settings panel */
.settings-panel {
    background: #020617;
    border-radius: 12px;
    padding: 0.9rem 1rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
    margin-bottom: 1rem;
}

.settings-panel h2 {
    margin: 0 0 0.5rem;
    font-size: 1rem;
}

.settings-row {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.4rem;
}

.settings-name-input {
    flex: 1;
    padding: 0.25rem 0.4rem;
    border-radius: 6px;
    border: 1px solid rgba(148, 163, 184, 0.7);
    background: #020617;
    color: #e5e7eb;
    font-size: 0.8rem;
}

/* Win% chart */
.winpct-chart {
    margin-top: 1rem;
    background: #020617;
    border-radius: 12px;
    padding: 0.8rem 1rem;
    border: 1px solid rgba(148, 163, 184, 0.4);
}

.winpct-chart h3 {
    margin: 0 0 0.4rem;
    font-size: 0.95rem;
}

.winpct-bars {
    display: flex;
    flex-direction: column;
    gap: 0.35rem;
}

.winpct-row {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
    gap: 0.4rem;
    font-size: 0.8rem;
}

.winpct-label {
    display: flex;
    align-items: center;
    gap: 0.35rem;
}

.winpct-bar-track {
    position: relative;
    height: 8px;
    border-radius: 999px;
    background: #111827;
    overflow: hidden;
}

.winpct-bar-fill {
    position: absolute;
    inset: 0;
    border-radius: 999px;
    background: linear-gradient(90deg, #22c55e, #eab308);
}

.winpct-value {
    min-width: 3rem;
    text-align: right;
}

/* Modal */
.modal-backdrop {
    position: fixed;
    inset: 0;
    background: rgba(15, 23, 42, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 40;
}

.modal {
    background: #020617;
    border-radius: 16px;
    padding: 1rem 1.4rem 1.2rem;
    border: 1px solid rgba(250, 204, 21, 0.6);
    width: 280px;
    max-width: 90%;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.6);
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.4rem;
}

.modal-title {
    font-weight: 600;
    font-size: 0.95rem;
}

.modal-close {
    border: none;
    background: transparent;
    color: #e5e7eb;
    cursor: pointer;
    font-size: 1rem;
}

.modal-body {
    text-align: center;
    padding-top: 0.3rem;
}

.winner-trophy {
    font-size: 2rem;
    margin-bottom: 0.2rem;
}

.winner-name {
    margin: 0.4rem 0 0.1rem;
}

.winner-stats {
    margin: 0;
    font-size: 0.85rem;
    opacity: 0.85;
}

/* Stack sidebar panels nicely */
.sidebar .standings+.standings,
.sidebar .settings-panel+.standings,
.sidebar .standings+.winpct-chart {
    margin-top: 0.8rem;
}